
.o-content-width {
	margin: 0 auto;
	max-width: 100em;
}

header {
	margin: 0 0 4em;
	text-align: center;
}

.filter-heading,
.filter-list {
	display: inline-block;
}

.filter-list li {
	display: inline-block;
	margin-right: 1em;
}

/*-- Start general styles --*/

	.wrapper {
		position: relative;
		perspective: 600px;
	}

	.bubble-chart-container,
	.overall-charts-container {
		float: left;
	}


	.bubble-chart-container {
		transform-style: preserve-3d;
		transition: transform 1s;
		width: 60%;
		height: 80vh;
		/* proportions of map of netherlands is 1:1.175 or 0.85:1 */
		min-width: 500px;
		min-height: 600px;
	}

	.map-view .bubble-chart-container {
		transform: rotateX(45deg) rotateZ(10deg);
	}

	.bubble-chart {
		display: block;
		margin: 0 auto;
		width: 100%;
		height: 100%;
		/*background: lightblue;*/
	}

	.overall-charts-container {
	}

	.legends {
		position: absolute;
		top: 0;
		right: 0;
	}

	.info-box {
		opacity: 0;
		transition: opacity 0.5s;
	}

	.employee {
		fill: $color-blue;
		transition: fill 0.5s, r 2s;

		.employee-group & {
			stroke: $color-dark-grey;
			stroke-width: 2px;
		}
	}

	.map-view .info-box--hometown,
	.map-view .info-box--offices {
		opacity: 1;
	}

	.axis .domain {
		stroke: transparent;
	}

	.value {
		color: $color-blue;;
	}

	.value--primary {
		font-size: 3rem;
		font-weight: bold;
	}

/*-- End general styles --*/





.highlight-gender {
	.employee--female,
	.employee--vrouw {
		fill: pink;
	}

	.employee--male,
	.employee--man {
		fill: $color-blue;
	}
}

.highlight-office {

	g {// add g-selector for specificity

		.employee--office-utrecht {
			fill: $color-utrecht;
		}
		
		.employee--office-amsterdam {
			fill: $color-amsterdam;
		}
		
		.employee--office-amersfoort {
			fill: $color-amersfoort;
		}
		
		.employee--office-eindhoven {
			fill: $color-eindhoven;
		}	}

}
